import {NotificationBuilder} from "../../ui/notifications";
export default function (event) {

    // let notification = event.data;

    console.log(event);

    let builder:NotificationBuilder = new NotificationBuilder();
    builder
        .setText(event.text)
        .setType(event.type);

    for (let i = 0; i < event.actions.length; i++) {
        builder
            .addAction()
            .setText(event.actions[i].text)
            .setUrl(event.actions[i].action);
    }

    builder
        .notify();

}